.app {
    padding-top: 80px !important;
    min-height: 760px !important;
    margin-bottom: 164px;

    .container {

        .content {
            text-align: center;

            .title {
                font-size: 40px;
                color: #717171;
                font-weight: bold;
                word-spacing: 4px;
            }

            .box {
                position: relative;
                margin-bottom: 40px;

                .button {
                    background-color: transparent;
                    border: none;
                    text-decoration: underline;
                    font-size: 40px;
                    font-weight: 600;
                    margin-top: 16px;
                    cursor: pointer;

                    .btnIcon {
                        font-size: 24px;
                        display: inline-flex;
                        justify-content: center;
                        align-items: center;
                        margin-left: 8px;
                        animation: rotate-reverse .2s linear;
                    }
                }

                .list {
                    position: absolute;
                    top: 100%;
                    left: 50%;
                    transform: translateX(-50%) scale(1.5);
                    transform-origin: top;
                    z-index: 3;
                    max-height: 400px;
                    overflow-y: scroll;
                    background-color: #fff;
                    border: 1px solid #000;
                    display: none;

                    .item {
                        padding: 18px 36px 18px 18px;
                        cursor: pointer;
                        text-align: left;
                        position: relative;
                    }

                    .item:hover {
                        background-color: rgb(34, 34, 34);
                        color: rgb(255, 255, 255);
                    }

                    .actived.item {
                        transition: color 0.02s ease 0s, background-color 0.02s ease 0s;
                        border-bottom: 1px solid rgb(221, 221, 221);
                        background-color: rgb(34, 34, 34);
                        color: rgb(255, 255, 255);
                    }

                    .itemIcon {
                        position: absolute;
                        right: 0;
                        top: 50%;
                        display: inline-flex;
                        visibility:hidden;
                        transform: translate(-70%, -50%);
                    }

                    .actived.item > .itemIcon {
                        visibility: visible;
                    }
                }
            }

            .total {
                font-size: 40px;
                font-weight: bold;
            }

            .footer {
                text-decoration: underline;
                font-weight: 600;
                margin-top: 40px;
                cursor: pointer;
            }
        }
    }
}

