                        transform-origin: top;
                        z-index: 3;
                        max-height: 400px;
                        overflow-y: scroll;
                        background-color: #fff;
                        border: 1px solid #000;
                        display: none;
    
                        .item {
                            padding: 18px 36px 18px 18px;
                            cursor: pointer;
                            text-align: left;
                            position: relative;
                        }
    
                        .item:hover {
                            background-color: rgb(34, 34, 34);
                            color: rgb(255, 255, 255);
                        }
    
                        .actived.item {
                            transition: color 0.02s ease 0s, background-color 0.02s ease 0s;
                            border-bottom: 1px solid rgb(221, 221, 221);
                            background-color: rgb(34, 34, 34);
                            color: rgb(255, 255, 255);
                        }
    
                        .itemIcon {
                            position: absolute;
                            right: 0;
                            top: 50%;
                            display: inline-flex;
                            visibility:hidden;
                            transform: translate(-70%, -50%);
                        }
    
                        .actived.item > .itemIcon {
                            visibility: visible;
                        }
                    }
                }
    
                .total {
                    font-size: 40px;
                    font-weight: bold;
                }
    
                .footer {
                    text-decoration: underline;
                    font-weight: 600;
                    margin-top: 40px;
                    cursor: pointer;
                }
            }
        }
    }
}

.displayBlock {
    display: block !important;
}


@keyframes rotate {
    from { transform: rotate(0deg) }
    to { transform: rotate(180deg) }
}
@keyframes rotate-reverse {
    from { transform: rotate(180deg) }
    to { transform: rotate(0deg) }
}
.rotate {
    animation: rotate .2s linear forwards !important;
}